@import url(https://fonts.googleapis.com/css?family=La+Belle+Aurore);
@import url(https://fonts.googleapis.com/css?family=B612);
@import url(https://fonts.googleapis.com/css?family=Cairo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/*--------- Project Card --------*/

.project-card-frame {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.project-card {
	width: 100%;
	height: 200px;
	border-radius: 15px;

	display: -webkit-flex;

	display: flex;
	-webkit-align-items: flex-end;
	        align-items: flex-end;
	margin: 15px 10px;
	overflow: hidden;
	box-shadow: 0 0 15px rgba(0,0,0,0.5);
	transition: 300ms ease-in-out;
}
.project-card:hover {
	box-shadow: 0 0 20px rgba(0,0,0,0.8);
}

.project-card .content {
	margin-right: 0.2em;
}

.project-card .details {
	width: 100%;
	
	display: -webkit-flex;
	
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 5px 10px;

	text-align: left;
	font-weight: 100;
	font-size: 30px;

	background: rgba(78, 66, 26, 0.5);
	color: #fff;
}

.project-card .details .buttons {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-left: auto;
}

.project-card a, .project-card a:hover {
	text-decoration: none;
	color: inherit;
}

.project-card button {
	margin-bottom: 10px;
	text-transform: none;
	font-weight: bolder;
	color: white;
	box-shadow: 2px 2px 2px 1px rgb(58, 58, 58);
}
.project-card button:hover {
	font-weight: bold;
	box-shadow: 0 0 20px rgba(161, 0, 54, 0.8);
}

.project-card h1 {
	font-size: 0.6em;
}
.project-card p {
	font-size: 0.55em;
	font-style: italic;
}

@media only screen and (min-width: 768px) {
	.project-card h1 {
		font-size: 0.8em;
	}
	.project-card p {
		font-size: 0.65em;
	}
}

@media only screen and (min-width: 992px) {
	.project-card {
		width: 70%;
		height: 250px;
		margin: 30px 15px;
	}
	.project-card:hover {
		-webkit-transform: scale(1.02);
		        transform: scale(1.02);
	}

	.project-card .details {
		padding: 0 20px;
	}

	.project-card .details .buttons {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
	.project-card button {
		margin-left: 10px;		
	}

	.project-card h1 {
		font-size: 0.85em;
	}
	.project-card p {
		font-size: 0.7em;
	}
}

/*--------- Category Card --------*/

.category-card-frame {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.category-card {
	width: 300px;
	height: 300px;
	border-radius: 3px;

	margin: 15px;
	overflow: hidden;

	text-align: center;
	box-shadow: 0 0 15px rgba(0,0,0,0.5);
	transition: 300ms ease-in-out;
}
.category-card:hover {
	box-shadow: 0 0 20px rgba(0,0,0,0.8);
}

.category-card h1 {
	padding: 25px;
	font-size: 25px;
	color: #fff;
	background: rgba(78, 66, 26, 0.3);
}

.poster {
	width: 80px;
	height: 80px;
	border-radius: 100px;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 100px auto;
	transition: 300ms ease-in-out;
}
.poster:hover {
	text-decoration: none;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);	
}

.poster-icon {
	margin-top: -25px;
	margin-bottom: -22px;
	font-size: 58px;
	color: #fff;
}
.poster span {
	color: white;
}

.button-0 {
	background: #A770EF;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #FDB99B, #CF8BF3, #A770EF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	box-shadow: 0 0 20px purple;
}
.button-1 {
	background: #4776E6;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #8E54E9, #4776E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	box-shadow: 0 0 20px lightblue;
}
.button-2 {
	background: #AA076B;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #61045F, #AA076B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	box-shadow: 0 0 20px purple;
}
.button-3 {
	background: #00c3ff;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #ffff1c, #00c3ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	box-shadow: 0 0 20px olive;
}

@media only screen and (min-width: 768px) {
	.category-card:hover {
		-webkit-transform: scale(1.02);
		        transform: scale(1.02);	
	}

	.poster-icon {
		margin-top: -15px;
	}
}

/*** Universal Fonts ***/
h1, h2, h3 {
  font-family: 'B612', Georgia;
}

h1 {
  font-size: 1.6em;
}

h2 {
  font-size: 1.2em;
}

h3 {
  font-size: 1.1em;
}

p {
  font-family: 'Cairo', Verdana;
  font-size: 1em;
}


/*** Links ***/
a {
  color: #fd466e;
}


/*** Navbar ***/
.navbar-brand {
  font-family: 'La Belle Aurore', 'Comic Sans MS';
  font-size: xx-large;
  font-weight: bold;
  color: #ff5f09 !important;
}

.navbar-toggler {
  width: 3.25em;
  height: 1.25em;
  padding: 0;
}
.navbar-toggler-icon {
  height: 1.25em;
}

.navbar-nav a {
  color: #fd466e !important;
}
.navbar-nav a:hover {
  color: #fd6411 !important;
}
.navbar-nav a.active {
  color: #ff5f09 !important;
  text-decoration: underline;
}


/*** Jumbotron ***/
.jumbotron {
  background: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.8)
  );
  position: relative;
}
.jumbotron .content {
  text-shadow: 0px 0px 30px #000;
  color: white;
}

.self-intro p {
  font-size: 1.15em;
  letter-spacing: 0.1em;
  text-shadow: 0 0 10px purple;
}

.self-intro .intro-highlight {
  white-space: nowrap;
  font-weight: 900;
  font-style: italic;
  color: gold;
}

.badge-group {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 2em;
}
.badge-item img {
  height: 5em;
  width: 5em;
}


/* Bottom jumbotron wavy boundary */
.jumbo-wave-edge {
  position: absolute;
  bottom: -0.4px;   /* This is to completely cover the bottom of the jumbo since "bottom: 0" does not fully hide the bottom of the image */
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.jumbo-wave-edge svg {
  position: relative;
  display: block;
  width: calc(124% + 1.3px);
  height: 50px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.jumbo-wave-edge .shape-fill {
  fill: hsl(0, 0%, 100%);
}


/*** Buttons ***/
button {
  background: linear-gradient(45deg, #FF8E53 30%, #FE6B8B 80%);
  width: 100px;
  box-shadow: 2px 2px 2px #ccc;
}
button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}


/*** SideNav ***/
.sidenav {
  width: 100px;
  position: fixed;
  z-index: 1;
  top: 30%;
  left: 2%;
}
.sidenav a {
  text-decoration: none;
  display: block;
}
.sidenav .fa, .mobile-nav .fa {
  color: #fd6411;
}
.sidenav span, .mobile-nav span {
  font-size: 15px;
  color: #fd466e;
}
.sidenav a:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


/* Medium devices (iPad, 768px and up) */
@media only screen and (min-width: 768px) {
  h1 {
    font-size: 2.25em;
  }

  h2 {
    font-size: 1.8em;
  }

  h3 {
    font-size: 1.7em;
  }
  
  p {
    font-size: 1.25em;
  }

  .self-intro {
    margin: 1em auto 1.5em;
  }

  .badge-group {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 3em;
  }
  .badge-item img {
    height: 6em;
    width: 6em;
  }

  .resume-carousel {
    width: 75%;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .container {
    max-width: 800px;
  }

  .jumbotron {
    height: 35em;
  }

  .self-intro p {
    width: 70ch;
    font-size: 1.25em;
  }

  .resume-carousel {
    width: 50%;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1152px) {
  .container {
    max-width: 960px;
  }

  .badge-group {
    float: right;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: -21em;
  }
  .badge-group > * + * {
    margin-top: 1em;
  }
  .badge-item img {
    height: 6.5em;
    width: 6.5em;
  }

  .info-card {
    width: 75%;
  }
}
@media only screen and (min-width: 1362px) {
  .container {
    max-width: 1140px;
  }
}
@media only screen and (min-width: 1600px) {
  .sidenav {
    left: 5%;
  }
}
