/*--------- Project Card --------*/

.project-card-frame {
	width: 100%;
	display: flex;
	justify-content: center;
}

.project-card {
	width: 100%;
	height: 200px;
	border-radius: 15px;

	display: flex;
	align-items: flex-end;
	margin: 15px 10px;
	overflow: hidden;

	-webkit-box-shadow: 0 0 15px rgba(0,0,0,0.5);
	box-shadow: 0 0 15px rgba(0,0,0,0.5);

	-webkit-transition: 300ms ease-in-out;
	-o-transition: 300ms ease-in-out;
	transition: 300ms ease-in-out;
}
.project-card:hover {
	-webkit-box-shadow: 0 0 20px rgba(0,0,0,0.8);
	box-shadow: 0 0 20px rgba(0,0,0,0.8);
}

.project-card .content {
	margin-right: 0.2em;
}

.project-card .details {
	width: 100%;
	
	display: flex;
	align-items: center;
	padding: 5px 10px;

	text-align: left;
	font-weight: 100;
	font-size: 30px;

	background: rgba(78, 66, 26, 0.5);
	color: #fff;
}

.project-card .details .buttons {
	display: flex;
	flex-direction: column;
	margin-left: auto;
}

.project-card a, .project-card a:hover {
	text-decoration: none;
	color: inherit;
}

.project-card button {
	margin-bottom: 10px;
	text-transform: none;
	font-weight: bolder;
	color: white;
	box-shadow: 2px 2px 2px 1px rgb(58, 58, 58);
}
.project-card button:hover {
	font-weight: bold;
	box-shadow: 0 0 20px rgba(161, 0, 54, 0.8);
}

.project-card h1 {
	font-size: 0.6em;
}
.project-card p {
	font-size: 0.55em;
	font-style: italic;
}

@media only screen and (min-width: 768px) {
	.project-card h1 {
		font-size: 0.8em;
	}
	.project-card p {
		font-size: 0.65em;
	}
}

@media only screen and (min-width: 992px) {
	.project-card {
		width: 70%;
		height: 250px;
		margin: 30px 15px;
	}
	.project-card:hover {
		transform: scale(1.02);
	}

	.project-card .details {
		padding: 0 20px;
	}

	.project-card .details .buttons {
		flex-direction: row;
	}
	.project-card button {
		margin-left: 10px;		
	}

	.project-card h1 {
		font-size: 0.85em;
	}
	.project-card p {
		font-size: 0.7em;
	}
}
