@import url('https://fonts.googleapis.com/css?family=La+Belle+Aurore');
@import url('https://fonts.googleapis.com/css?family=B612');
@import url('https://fonts.googleapis.com/css?family=Cairo');

/*** Universal Fonts ***/
h1, h2, h3 {
  font-family: 'B612', Georgia;
}

h1 {
  font-size: 1.6em;
}

h2 {
  font-size: 1.2em;
}

h3 {
  font-size: 1.1em;
}

p {
  font-family: 'Cairo', Verdana;
  font-size: 1em;
}


/*** Links ***/
a {
  color: #fd466e;
}


/*** Navbar ***/
.navbar-brand {
  font-family: 'La Belle Aurore', 'Comic Sans MS';
  font-size: xx-large;
  font-weight: bold;
  color: #ff5f09 !important;
}

.navbar-toggler {
  width: 3.25em;
  height: 1.25em;
  padding: 0;
}
.navbar-toggler-icon {
  height: 1.25em;
}

.navbar-nav a {
  color: #fd466e !important;
}
.navbar-nav a:hover {
  color: #fd6411 !important;
}
.navbar-nav a.active {
  color: #ff5f09 !important;
  text-decoration: underline;
}


/*** Jumbotron ***/
.jumbotron {
  background: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.8)
  );
  position: relative;
}
.jumbotron .content {
  text-shadow: 0px 0px 30px #000;
  color: white;
}

.self-intro p {
  font-size: 1.15em;
  letter-spacing: 0.1em;
  text-shadow: 0 0 10px purple;
}

.self-intro .intro-highlight {
  white-space: nowrap;
  font-weight: 900;
  font-style: italic;
  color: gold;
}

.badge-group {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 2em;
}
.badge-item img {
  height: 5em;
  width: 5em;
}


/* Bottom jumbotron wavy boundary */
.jumbo-wave-edge {
  position: absolute;
  bottom: -0.4px;   /* This is to completely cover the bottom of the jumbo since "bottom: 0" does not fully hide the bottom of the image */
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.jumbo-wave-edge svg {
  position: relative;
  display: block;
  width: calc(124% + 1.3px);
  height: 50px;
  transform: rotateY(180deg);
}

.jumbo-wave-edge .shape-fill {
  fill: hsl(0, 0%, 100%);
}


/*** Buttons ***/
button {
  background: linear-gradient(45deg, #FF8E53 30%, #FE6B8B 80%);
  width: 100px;
  box-shadow: 2px 2px 2px #ccc;
}
button:hover {
  transform: scale(1.05);
}


/*** SideNav ***/
.sidenav {
  width: 100px;
  position: fixed;
  z-index: 1;
  top: 30%;
  left: 2%;
}
.sidenav a {
  text-decoration: none;
  display: block;
}
.sidenav .fa, .mobile-nav .fa {
  color: #fd6411;
}
.sidenav span, .mobile-nav span {
  font-size: 15px;
  color: #fd466e;
}
.sidenav a:hover {
  transform: scale(1.1);
}


/* Medium devices (iPad, 768px and up) */
@media only screen and (min-width: 768px) {
  h1 {
    font-size: 2.25em;
  }

  h2 {
    font-size: 1.8em;
  }

  h3 {
    font-size: 1.7em;
  }
  
  p {
    font-size: 1.25em;
  }

  .self-intro {
    margin: 1em auto 1.5em;
  }

  .badge-group {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 3em;
  }
  .badge-item img {
    height: 6em;
    width: 6em;
  }

  .resume-carousel {
    width: 75%;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .container {
    max-width: 800px;
  }

  .jumbotron {
    height: 35em;
  }

  .self-intro p {
    width: 70ch;
    font-size: 1.25em;
  }

  .resume-carousel {
    width: 50%;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1152px) {
  .container {
    max-width: 960px;
  }

  .badge-group {
    float: right;
    display: flex;
    flex-direction: column;
    margin-top: -21em;
  }
  .badge-group > * + * {
    margin-top: 1em;
  }
  .badge-item img {
    height: 6.5em;
    width: 6.5em;
  }

  .info-card {
    width: 75%;
  }
}
@media only screen and (min-width: 1362px) {
  .container {
    max-width: 1140px;
  }
}
@media only screen and (min-width: 1600px) {
  .sidenav {
    left: 5%;
  }
}